.app {
  display: flex;
  min-height: 100vh;
}

.main-content {
  flex: 1;
  transition: margin-left 0.3s ease;
  min-height: 100vh;
  background: #f8fafc;
}

.main-content.sidebar-open {
  margin-left: 0;
}

@media (min-width: 768px) {
  .main-content.sidebar-open {
    margin-left: 280px;
  }
}

/* Mobile menu button */
.mobile-menu-btn {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1001;
  background: #4f46e5;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px;
  cursor: pointer;
  display: none;
  font-size: 1.2rem;
}

@media (max-width: 767px) {
  .mobile-menu-btn {
    display: block;
  }
  
  .main-content.sidebar-open {
    margin-left: 0;
  }
}